<template>
  <div>
    <p>Excel</p>
    <el-form 
      :model="ruleForm"
      size="mini" ref="ruleForm" style="padding: 10px 10px 0;"
      label-width="140px" class="demo-ruleForm" enctype="multipart/form-data">
      <el-form-item label="excel" prop="excelPath">
        <el-input v-model="ruleForm.excelPath" @change.native="inputChange" type="file" name="file"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="importData('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
    <!-- <form enctype="multipart/form-data" method="post" action="/api/excelImport">    
      Excel<input type="file" name="file"/><br/>    
      <input type="submit" value=""/>
    </form> -->
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  export default {
    computed:{
      ...mapState(['userInfo'])
    },
    data() {
      return {
        ruleForm: {
          excelPath: ''
        },
        theFile: ''
      }
    },
    methods: {
      inputChange(e){
        // event
         // console.log('',e)
        this.theFile = e.target.files[0]
      },
      importData() {
        // 
        //FormDate
        let formData = new FormData();
        //
        formData.append("file", this.theFile);
        formData.append("owner", this.userInfo.id);
        //  // console.log(FormData.get("multipartFile"));
        var config={
            headers: {
                "Content-Type": "multipart/form-data"
            }
        };
        this.$axios.post(
          `${window.baseUrl}/importExcelbb`,
          formData,config
        ).then((res) => {          //ES6
           // console.log('response:', res)       //
          if (res.data && res.data.code==200) {
            this.$message({
              message: '',
              type: 'success'
            })
            
          } else {
            this.$message({
              message: '',
              type: 'error'
            })
          }

        }).catch((error) => {
           // console.log(error)       //
          this.$message({
            message: '',
            type: 'error'
          })
        })
      }
    }
  }
</script>