import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

//*
function storeLocalStore (name,data) {
  if(typeof data =='object'){
    if(data.password){
      delete data.password;
    }
    window.localStorage.setItem(name,JSON.stringify(data));
  }else{
    window.localStorage.setItem(name,data);
  }
}

